@font-face {
  font-family: "Graphik";
  src: url(../src/fonts/Graphik-Regular.otf) format("opentype") ;
}
@font-face {
  font-family: "Graphik2";
  src: url(../src/fonts/Graphik-Bold.otf) format("opentype");
}
@font-face {
  font-family: "Graphik3";
  src: url(../src/fonts/Graphik-Light.otf) format("opentype");
}

@font-face {
  font-family: "Graphik4";
  src: url(../src/fonts/Graphik-Semibold.otf) format("opentype");
}


.App {
text-align: center;
}

h1 {
  font-family: "Graphik2", Arial, Helvetica, sans-serif;
}

p {
font-family: "Graphik", Arial, Helvetica, sans-serif;
}

#login {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.items >* {  
  margin: 1.5em;
}

.logo {
  max-width: 5em;
  max-height: 3em;
}
.logo-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.google {
  background-color:  #dd4b39;
  color: #fff;
}

.facebook {
  background-color:  #3b5998;
  color: #fff;  
}

.navbar{
  margin-bottom: 0;
}

.glyphicon{
  font-size: 1.2em;
  color: orange;
  margin-right: 5px;
}


